

/* Header */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 50px;
  background: white;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
  position: relative;
  z-index: 1000; /* Ensure navbar is above all other content */
}

.logo {
  display: flex;
  align-items: center;
}

.logo img {
  width: 80px;
  height: auto;
  margin-right: 10px;
}

.nav-links {
  list-style: none;
  display: flex;
  gap: 20px;
}

.nav-links li {
  font-size: 18px;
}

.nav-links a {
  text-decoration: none;
  color: black;
  font-weight: bold;
}

.nav-links a:hover {
  color: blue;
}

/* Mobile Responsive */
.menu-toggle {
  display: none;
  font-size: 28px;
  cursor: pointer;
}

@media screen and (max-width: 768px) {
  .nav-links {
    display: none;
    flex-direction: column;
    position: absolute;
    top: 60px;
    right: 0;
    background: white;
    width: 100%;
    text-align: center;
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
    z-index: 999; /* Ensure dropdown is above other elements */
  }

  .nav-links.active {
    display: flex;
  }

  .menu-toggle {
    display: block;
    z-index: 1001; /* Ensure menu icon is clickable */
  }
}

/* Extra Small Screens (480px and below) */
@media screen and (max-width: 480px) {
  .navbar {
    padding: 10px 20px;
  }

  .logo img {
    width: 70px;
  }

  .menu-toggle {
    font-size: 24px;
  }

  .nav-links {
    width: 100%;
    top: 50px;
  }

  .nav-links li {
    font-size: 16px;
    padding: 10px 0;
  }
}


  /*  Header End */
  /* Footer */

  .footer {
    background: #f8f8f8;
    padding: 50px 20px;
    text-align: center;
    border-top: 1px solid #ddd;
    margin-top: 50px;
  }
  
  /* Footer Content - Responsive */
  .footer-content {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding-bottom: 20px;
    margin-left: auto;
    margin-right: auto;
    max-width: 1200px;
  }
  
  .footer-section {
    flex: 1;
    min-width: 250px;
    margin-bottom: 20px;
    text-align: left;
  }
  
  .footer-section h2,
  .footer-section h3 {
    font-size: 20px;
    margin-bottom: 10px;
  }
  
  .footer-about {
    max-width: 300px;
  }
  
  .footer-section p,
  .footer-section ul {
    font-size: 16px;
    color: #555;
    text-align: justify;
  }
  
  .footer-section ul {
    list-style: none;
    padding: 0;
  }
  
  .footer-section ul li {
    margin: 5px 0;
  }
  
  .footer-section ul li a {
    text-decoration: none;
    color: #333;
  }
  
  /* Footer Bottom */
  .footer-bottom {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    padding-top: 10px;
    border-top: 1px solid #ddd;
    font-size: 14px;
  }
  
  .footer-bottom a {
    text-decoration: none;
    color: #333;
    font-weight: bold;
  }
  
  /* --- RESPONSIVE STYLES --- */
  
  /* Tablet View */
  @media (max-width: 768px) {

   

    .footer-content {
      flex-direction: column;
      align-items: center;
      text-align: center;
      margin-left: 0;
    }
  
    .footer-section {
      text-align: center;
      max-width: 100%;

    }
  
    .footer-about {
      max-width: 100%;
    }
    .footer-section ul{
      text-align: center;
    }

  
    .footer-bottom {
      flex-direction: column;
      text-align: center;
    }
  }
  
  /* Mobile View (480px) */
  @media (max-width: 480px) {
    .footer {
      padding: 30px 15px;
    }
  
    .footer-section h2,
    .footer-section h3 {
      font-size: 18px;
    }
  
    .footer-section p,
    .footer-section ul {
      font-size: 14px;
    }
  
    .footer-bottom {
      font-size: 12px;
      flex-direction: column;
      gap: 10px;
    }
  }
  

  /* Footer Ends */
  
  /* Home Page */
  .home {
    width: 100%;
    height: 500px;
    position: relative;
  }

  .swiper-container {
    width: 100%;
    height: 100%;
  }

  .slide {
    position: relative;
    width: 100%;
    height: 100%;
  }

  .slide-image {
    width: 100%;
    height: 90%;
    object-fit: cover;
  }

  .slide-content {
    position: absolute;
    top: 50%;
    left: 15%;
    transform: translateY(-50%);
    color: #fff;
    padding: 20px;
    text-align: center;
  }

  .cta-button {
    width: 150px;
    margin-top: 10px;
    padding: 10px 20px;
    background: #ff6600;
    color: white;
    border: none;
    cursor: pointer;
    font-size: 16px;
    border-radius: 5px;
  }

  .cta-button:hover {
    background: #cc5500;
  }

  .home .section2 {
    margin: 30px;
    width: 75%;
    text-align: center;
    left: 5%;
  }

  .home .section3 {
    margin: 150px 0px 50px 0px;
  }

  /* Features Section */
  .features-section {
    text-align: center;
    padding: 50px 20px;
    background-color: #f8f9fa;
  }

  .features-title {
    font-size: 28px;
    font-weight: bold;
    margin-bottom: 30px;
  }

  .features-container {
    display: flex;
    justify-content: center;
    gap: 20px;
    flex-wrap: wrap;
  }

  .feature-card {
    background: white;
    border-radius: 12px;
    padding: 20px;
    width: 220px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    text-align: center;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
  }

  .feature-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.15);
  }

  .feature-icon {
    font-size: 40px;
    margin-bottom: 15px;
  }

  .feature-title {
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 10px;
  }

  .feature-description {
    font-size: 14px;
    color: #555;
  }

  .features-button {
    width: 100px;
    margin-top: 20px;
    padding: 10px 20px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    font-size: 16px;
    transition: background 0.3s ease;
  }

  .features-button:hover {
    background-color: #0056b3;
  }

  /* About Section */
  .about-section {
    padding: 50px 20px;
    background-color: #ffffff;
  }

  .about-section-container {
    display: flex;
    justify-content: space-between;
    gap: 50px;
    flex-wrap: wrap;
    margin: 0 50px;
  }

  .image-container {
    flex: 1;
    max-width: 45%;
  }

  .feature-image {
    width: 90%;
    border-radius: 12px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease;
  }

  .feature-image:hover {
    transform: scale(1.05);
  }

  .content-container {
    flex: 1;
    max-width: 50%;
  }

  .section-title {
    font-size: 26px;
    font-weight: bold;
    color: #333;
    margin-bottom: 15px;
  }

  .section-text {
    text-align: justify;
    width: 75%;
    font-size: 18px;
    color: #555;
    line-height: 1.6;
    margin-bottom: 20px;
  }

  .section-button {
    width: 150px;
    padding: 10px 20px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    font-size: 16px;
    transition: background 0.3s ease;
  }

  .section-button:hover {
    background-color: #0056b3;
  }

  /* Management Section */
  .management-section {
    padding: 50px 20px;
    text-align: center;
  }

  .management-title {
    font-size: 22px;
    font-weight: bold;
    color: #2c3e50;
    margin-bottom: 20px;
    text-decoration: underline;
  }

  .management-container {
    display: flex;
    background-color: #eee9e8;
    border-radius: 80px 0px 80px 0px;
    padding: 30px;
    flex-wrap: wrap;
    justify-content: center;
  }

  .management-card {
    flex: 1;
    text-align: center;
    min-width: 200px;
    max-width: 300px;
    margin: 10px;
  }

  .management-card h3 {
    font-size: 20px;
    font-weight: bold;
    color: #000;
    margin-bottom: 8px;
  }

  .management-card p {
    font-size: 18px;
    color: #555;
    line-height: 1.5;
  }

  .bg-image {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .bg-image .bg {
    width: 75%;
    border: 1px solid #000;
    margin-bottom: 50px;
  }

  .end-section {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 50px;
  }

  .end-section .content {
    width: 80%;
    height: 180px;
    background-color: #70a4dc;
    margin: 50px;
    border-radius: 20px;
  }

  .call-container {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 40px;
  }

  .call-container .call {
    width: 150px;
    height: 30px;
    background-color: #ddd;
    border-radius: 10px;
  }

  /* Responsive Design */
  @media (max-width: 1024px) {
    .about-section-container {
      flex-direction: column;
      text-align: center;
    }

    .image-container,
    .content-container {
      max-width: 100%;
    }

    .section-text {
      width: 100%;
    }
  }

  @media (max-width: 768px) {

    .section2 p {
      text-align: justify;
      width: 90%;
      
    }

    .section3{
      margin-left: 50px;
    }

    .features-section{
      margin-left: 20px;
      margin-top: -150px;
    }
    .features-container {
      flex-direction: column;
      width: 90%;
    }
   

    .management-container {
      flex-direction: column;
      align-items: center;
    }

    .management-card {
      max-width: 100%;
      margin-bottom: 20px;
    }

    .feature-card {
      width: 90%;
    }

    .swiper-container,
    .slide-image {
      height: auto;
    }

    .slide-content {
      position: relative;
      top: auto;
      left: auto;
      transform: none;
      margin-top: -250px;
    }
  }
  @media (max-width: 480px) {
    /* General Adjustments */
    .home {
      height: auto;
      padding-bottom: 20px;
    }
  
    .swiper-container {
      height: 250px;

    }
    .slide-image{
      height: auto;
    }
  
    .slide-content {
      left: 10%;
      width: 80%;
      font-size: 14px;
    }
  
    .cta-button {
      width: 120px;
      padding: 8px 16px;
      font-size: 14px;
    }

    
  
    /* Features Section */
 
    .features-container {
      flex-direction: column;
      align-items: center;
      margin-left: -70px;
    }
  
    .feature-card {
      width: 90%;
      padding: 15px;
    }
  
    /* About Section */
    .about-section-container {
      flex-direction: column;
      align-items: center;
      gap: 20px;
      margin: 0px;
    }
  
    .image-container, .content-container {
      max-width: 100%;
      text-align: center;
    }
  
    .feature-image {
      width: 100%;
    }
  
    .section-text {
      width: 90%;
      font-size: 16px;
    }
  
    .section-button {
      width: 130px;
      font-size: 14px;
    }
  
    /* Management Section */
    .management-container {
      flex-direction: column;
      align-items: center;
      padding: 20px;
    }
  
    .management-card {
      width: 90%;
      text-align: center;
      margin-bottom: 20px;
    }
  
    /* Background Image */
    .bg-image {
      flex-direction: column;
      align-items: center;
    }
  
    .bg {
      width: 90%;
    }
  
    /* End Section */
    .end-section .content {
      width: 90%;
      height: auto;
      padding: 20px;
    }
  
    .call-container .call {
      width: 120px;
      height: 25px;
      font-size: 14px;
    }
  }
  